import { Component, OnInit } from '@angular/core';
import { SizesServices } from '../../../services/sizes/sizes.services';
import { ActivatedRoute, Router } from '@angular/router';
import { RolesServices } from '../../../services/roles/roles.services';
import { NbToastrService } from '@nebular/theme';
import { CategoriseServices } from '../../../services/categories/categories.services';

@Component({
  selector: 'app-create-size',
  templateUrl: 'create-size.component.html',
  styleUrls: ['create-size.component.scss'],
})
export class CreateSizeComponent implements OnInit {

  newSize;
  sizeTypes;
  categories;
  isLoading = false;

  constructor(private sizesService: SizesServices,
    private categoriseServices: CategoriseServices,
    private activeRouter: ActivatedRoute,
    private roleService: RolesServices,
    private toaster: NbToastrService,
    private route: Router) {
  }

  ngOnInit() {
    this.getCategories();
    this.getSizeTypes();
    this.newSize = {
      size: '',
      category_id: 0,
      size_type_id: 0,
    };
  }

  createSize() {
    this.toggleButton();
    const status = this.validateCreateSize();
    if (status['status']) {
      this.sizesService.createSiz(this.newSize).subscribe(res => {
        this.toggleButton();
        this.toaster.show(res['msg'], 'Success', { status: 'success' });
        this.route.navigateByUrl('/sizes');
      }, err => {
        this.toggleButton();
        this.toaster.show(err.error.message, 'Error', { status: 'danger' });
      });

    } else {
      this.toggleButton();
      this.toaster.show(status['message'], 'Error', { status: 'danger' });
    }
  }

  validateCreateSize() {
    const state = {
      status: true,
      message: ''
    };

    if (this.newSize.size === '') {
      state['status'] = false;
      state['message'] = 'Please, Enter Name';
    }
    if (this.newSize.category_id === 0) {
      state['status'] = false;
      state['message'] = 'Please, Chose category';
    }
    if (this.newSize.size_type_id === 0) {
      state['status'] = false;
      state['message'] = 'Please, Chose type';
    }
    return state;
  }

  getCategories() {
    this.categoriseServices.selectedCategories('childes').subscribe(res => {
      this.categories = res['data'];
    }, error => {
    });
  }

  getSizeTypes() {
    this.sizesService.getSizeTypes().subscribe(res => {
      this.sizeTypes = res['data'];
    }, error => {
    });
  }

  toggleButton() {
    this.isLoading = !this.isLoading;
  }
}
