import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { CreateSizeComponent } from './create-size/create-size.component';
import { SizesComponent } from './index-size/sizes.component';
import { EditSizeComponent } from './edit-size/edit-size.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: '', component: SizesComponent },
    { path: 'create-size', component: EditSizeComponent },
    { path: 'edit-size/:id', component: EditSizeComponent },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SizesRoutingModule {
}
