import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RolesServices } from '../../../services/roles/roles.services';
import { NbToastrService } from '@nebular/theme';
import { SizesServices } from '../../../services/sizes/sizes.services';
import { CategoriseServices } from "../../../services/categories/categories.services";
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { isObject } from 'rxjs/internal/util/isObject';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';


@Component({
  selector: 'app-edit-admin',
  templateUrl: 'edit-size.component.html',
  styleUrls: ['edit-size.component.scss'],
})
export class EditSizeComponent implements OnInit {
  title: string = '';
  sizeFormGroup: FormGroup;
  sizeId: any;
  sizeTypes: any[];
  categories: any[];
  isLoading: boolean = false;
  autoLoad: boolean = false;
  selectedCategories: any[] = [];

  constructor(private sizesService: SizesServices,
    private categoriseServices: CategoriseServices,
    private activeRouter: ActivatedRoute,
    private roleService: RolesServices,
    private toaster: NbToastrService,
    private route: Router) {
    this.sizeFormGroup = new FormGroup({
      id: new FormControl(''),
      size: new FormControl('', Validators.required),
      category: new FormControl(),
      size_type: new FormControl('', Validators.required),
      category_ids: new FormControl('', Validators.required),
      size_type_id: new FormControl(''),
      created_at: new FormControl(''),
      updated_at: new FormControl('')
    })
  }

  ngOnInit() {
    this.sizeId = this.activeRouter.snapshot.params.id;

    if (this.sizeId) {
      this.title = 'Edit';
      this.getSize();
    } else {
      this.title = 'Add';
    }

    this.sizeFormGroup.get('category').valueChanges.pipe(distinctUntilChanged(), debounceTime(800)).subscribe(res => {
      if (!isObject(res)) {
        this.autoLoad = true;
        const meta = 'name_en=' + res;

        // if (this.sizeFormGroup.get('category_ids').value) {
        //   this.sizeFormGroup.controls['category_ids'].setValue('');
        // }

        this.getCategories(meta);
      } else {
        if (!this.selectedCategories.some(ele => ele?.id === res?.id)) {
          this.selectedCategories.push(res);
        }
        this.sizeFormGroup.get('category').setValue('');
        this.sizeFormGroup.get('category_ids').setValue(this.selectedCategories.map(ele => ele.id));
      }
    });

    this.sizeFormGroup.get('size_type').valueChanges.subscribe(res => {
      this.sizeFormGroup.get('size_type_id').setValue(res);
    });

    this.getCategories();
    this.getSizeTypes();
  }

  editSize() {
    if (this.sizeFormGroup.valid) {
      this.isLoading = true;
      const size: any = {
        id: this.sizeFormGroup.get('id').value,
        size: this.sizeFormGroup.get('size').value,
        category_ids: this.sizeFormGroup.get('category_ids').value,
        size_type_id: this.sizeFormGroup.get('size_type_id').value,
      }
      this.sizesService[this.sizeId ? 'editSize' : 'createSiz'](size).subscribe(res => {
        this.toaster.show(res['msg'], 'Success', { status: 'success' });
        this.route.navigateByUrl('/sizes');
      },
        err => {
          this.toaster.show(err.error.message, 'Error', { status: 'danger' });
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
        }
      );

    }
  }

  getCategories(meta?: string) {
    this.categoriseServices.getCategorise(meta).subscribe(
      res => {
        this.categories = res['data'];
      },
      error => {
        this.autoLoad = false;
      },
      () => {
        this.autoLoad = false;
      }
    );
  }

  getSize() {
    this.isLoading = true;
    this.sizesService.getSize(this.sizeId).subscribe(
      res => {
        this.sizeFormGroup.patchValue(res['data']);
        this.sizeFormGroup.get('size_type').patchValue(res['data'].size_type?.id);
        this.selectedCategories = res['data']?.categories;
        this.sizeFormGroup.get('category_ids').patchValue(res['data'].categories?.map((ele: any) => ele.id));
      },
      error => {
        this.isLoading = false;
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  getSizeTypes() {
    this.sizesService.getSizeTypes().subscribe(res => {
      this.sizeTypes = res['data'];
    }, error => {

    });
  }

  onTagRemove(event: any) {
    this.selectedCategories = this.selectedCategories.filter(ele => ele.id !== event.id);
    this.sizeFormGroup.get('category_ids').setValue(this.selectedCategories.map(ele => ele.id));
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.name_en
    } else {
      return value
    }
  }

  backToSizes() {
    this.route.navigate(['sizes'])
  }
}
