import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SizesServices } from '../../../services/sizes/sizes.services';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { CategoriseServices } from '../../../services/categories/categories.services';
import { gride } from './sizes.column';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { State } from '@progress/kendo-data-query';
import { saveAs } from 'file-saver';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
@Component({
    selector: 'app-size',
    templateUrl: 'sizes.component.html',
    styleUrls: ['sizes.component.scss'],
})
export class SizesComponent implements OnInit {
    @ViewChild('dialog') dialog: any;
    columnGrid = gride;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    loading: boolean = false;
    autoCompleteData: any = {
        name: '',
        list: []
    };
    lastMeta: string;
    currentDeleteSize: any;


    constructor(
        private sizeService: SizesServices,
        private toaster: NbToastrService,
        private categoriseServices: CategoriseServices,
        private sizesService: SizesServices,
        private convertFilter: FilterToQueryService,
        private router: Router,
        private dialogService: NbDialogService,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.getSizes();
        this.getSizeTypes();
    }


    getSizes(meta?: string) {
        this.loading = true;
        this.lastMeta = meta;
        this.sizeService.getSizes(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res['total'];
            },
            error => {
                this.loading = false;
            },
            () => {
                this.loading = false;
            },
        );
    }

    deleteSize(dialogRef: any) {
        this.loading = true;
        this.sizeService.deleteSize(this.currentDeleteSize.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', { status: 'success' });
            },
            error => {
                this.toaster.show(error.error.message, 'Error', { status: 'danger' });
                this.loading = false;
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getSizes(this.lastMeta);
                this.currentDeleteSize = null;
            }
        );
    }

    getCategories(meta?: string) {
        this.autoCompleteData.name = 'category';
        this.categoriseServices.getCategorise(meta).subscribe(
            res => {
                this.autoCompleteData.list = res['data'];
            },
            error => {
            },
            () => {
            }
        );
    }

    getSizeTypes() {
        this.autoCompleteData.name = 'size_type';
        this.sizesService.getSizeTypes().subscribe(
            res => {
                this.autoCompleteData.list = res['data'];
            },
            error => {
            },
            () => {
            }
        );
    }

    filterChange(event: State) {
        this.getSizes(this.convertFilter.convertToQuery(event));
    }

    autoCompleteFilterChangeCheck(event: any) {
        if (event?.fieldName === 'size_type') {
            this.getSizeTypes();
        }

        if (event?.fieldName === 'category') {
            const meta = 'name=' + event.value;
            this.getCategories(meta);
        }

    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./sizes/edit-size/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteSize = event.data;
                this.open(this.dialog);
                break;

        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'sizes/export/excel?' + this.lastMeta : 'sizes/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'sizes.xlsx');
        }),
            error => this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
    }
}
